<template>
  <div class="form-login">
    <el-form ref="form" :model="form">
      <el-form-item>
        <el-input v-model="form.email" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-input v-model="form.password" placeholder="" type="password"></el-input>
      </el-form-item>
      <el-form-item>
        <el-input v-model="form.password2" placeholder="" type="password"></el-input>
      </el-form-item>
      <el-form-item>
        <el-row>
          <el-col :span="22">
            <el-button type="primary" size="mini" @click="onSubmit"></el-button>
          </el-col>
          <el-col :span="2">
            <el-link type="warning" @click="onJump('login')"></el-link>
          </el-col>
        </el-row>
      </el-form-item>
    </el-form>

    <About></About>
  </div>
</template>

<script>
import About from "../commons/About.vue";
export default {
  components: { About },
  data() {
    return {
      form: {
        email: "",
        password: "",
        password2: ""
      }
    };
  },
  methods: {
    onSubmit() {
      if (this.form.email == "") {
        this.$notify.error({
          title: "",
          message: ""
        });
        return;
      }
      if (this.form.password.length < 6) {
        this.$notify.error({
          title: "",
          message: ""
        });
        return;
      }
      if (this.form.password2.length < 6) {
        this.$notify.error({
          title: "",
          message: ""
        });
        return;
      }
      if (this.form.password != this.form.password2) {
        this.$notify.error({
          title: "",
          message: ""
        });
        return;
      }

      this.$http
        .post("/auth/register", this.form)
        .then(res => {
          if (res.data.errno == 0) {
            this.$notify({
              title: "",
              message:
                "",
              type: "success",
              duration: 0
            });
            return;
          }
          this.$notify.error({
            title: "",
            message: res.data.msg
          });
        })
        .catch(err => {
          window.console.log(err);
        });
    },
    onJump(r) {
      this.$router.replace({ name: r });
    }
  }
};
</script>

<style scoped>
.form-login {
  width: 32%;
  margin: auto;
  padding-top: 20%;
}
</style>